import { ArticleService } from './../article.service';
import { Component, OnInit } from '@angular/core';

//declare jQuery as typescript doesn't know about the jQuery
declare var jQuery: any;//jQuery is available on window object is type of any
@Component({
  selector: 'app-article-list-header',
  templateUrl: './article-list-header.component.html',
  styleUrls: ['./article-list-header.component.css']
})
export class ArticleListHeaderComponent implements OnInit {

  //modified from private to public because of AOT
  public currentFilter: string = 'Time';
  public sortDirection: number = 1;//default descending order
  constructor(private articleService: ArticleService) { 
    console.log('ArticleListHeaderComponent - constructor')
  }

  ngOnInit() {
    jQuery('.ui.dropdown').dropdown();
  }

  changeDirection(){//1 or -1
    this.sortDirection = this.sortDirection * -1;
    this.updateSort();
  }

  changeSort(filter:string){
    console.log('changeSort:', filter);
    if(filter === this.currentFilter){
      this.changeDirection();
    }else {
      this.currentFilter = filter;
      this.updateSort();
    }
  }

  updateSort(){
    console.log('updateSort');
    this.articleService.sortBy(this.currentFilter, this.sortDirection);
  }

  
  liveSearch(evt){
    console.log('ArticleListHeader liveSearch');
    let val = evt.target.value;
    this.articleService.filterByTitle(val);
  }
}
