
interface ArticleJSON {
  author:string,
  title:string,
  description:string,
  publishedAt:string,
  url:string,
  urlToImage:string,
  votes:string
}

export class Article {
     /*
  private title: string;
  private description: string;

  constructor(title:string, description:string){
    this.title = title;
    this.description = description;
  }
  */
 

  //for converting json to article object for toPromise as its return object but we can use it if we want
  //but no need for Observable as it return json
  static fromJSON(json: ArticleJSON): Article {
    let article = Object.create(Article.prototype);
    return Object.assign(article, json, {
      votes: json.votes ? json.votes: 0,
      publishedAt: json.publishedAt ? new Date(json.publishedAt) : new Date()
    });
  }

  constructor(public title:string, public description:string,public urlToImage:string, public publishedAt: Date, public votes?:number){
    //console.log('article constructor');
    this.votes = votes | 0;
    this.publishedAt = new Date(publishedAt);
  }

  

  voteUp(): void{
    this.votes++;
  }

  voteDown(): void{
    this.votes--;
  }
}
