
import { Injectable } from '@angular/core';
import { Article } from './article';
import { HttpClient, HttpParams } from '@angular/common/http';
import { Newsresult } from './newsresult';
import { BehaviorSubject, Observable, combineLatest } from 'rxjs';
import { environment } from 'src/environments/environment';
import {map, filter} from 'rxjs/operators';

/**
 * Interface function
 * [].sort(compare(a,b))
 * return value
 * 0 === they are equal in sort
 * 1 === a comes before b
 * -1 === b comes before a * 
 */
interface ArticleSortFn {
  (a: Article, b: Article): number;
}

interface ArticleSortOrderFn {
  (direction: number): ArticleSortFn;
}
//ArticleSortOrderFn return ArticleSortFn return number
const sortByTime: ArticleSortOrderFn = (direction: number) => 
                                          (a: Article, b:Article) => 
                                          {
                                            console.log('### sortByTime - direction:', direction);
                                            return (direction * (b.publishedAt.getTime() - a.publishedAt.getTime()));
                                          }  
                                         
const sortByVote: ArticleSortOrderFn = (direction: number) => 
                                          (a: Article, b:Article) => 
                                          {
                                            console.log('### sortByVotes - direction:', direction);
                                            return (direction * (b.votes - a.votes));
                                          }                                            

const sortFns = {
  'Time': sortByTime,
  'Votes': sortByVote
}

@Injectable({
  providedIn: 'root'
})
export class ArticleService {
  
  private source = "reddit-r-all";
  private path = '/v2/top-headlines';
  private sourcePath = '/v2/sources'

  constructor(private httpClient: HttpClient) { 
    console.log('ArticleService constructor', this.orderedArticles);
    //added for updateSourceKey changes
    this.refreshSubject.subscribe(this.getArticleList.bind(this));
    

    //update order article subject
    //order article to be a combination of all articles plus filter
    this.orderedArticles = combineLatest(
                           this.articles, 
                           this._sortByFilterSubject, 
                           this._sortByDirectionSubject,
                           this._filterBySubject)
                           .pipe(map(([articles, sorter, direction, filterStr]) => {//any one of these update, map block will process and will return a new sorted list                              
                              console.log('ArticleService constructor - map - direction:',direction, filterStr);
                              const reg = new RegExp(filterStr, 'gi');//g global, all match / i insensitive, case insensitive match
                              return  articles
                                      .filter(article => reg.exec( article.title))
                                      .sort(sorter(direction));
                           }));
                           console.log('ArticleService constructor 2', this.orderedArticles);                           
  }

  getArticles() : Observable<Newsresult> {
    let params = new HttpParams().set('sources', this.source).set('apiKey', environment.apiKey);    
    return this.httpClient.get<Newsresult>(environment.baseUrl, {params});
  }

  private _articles: BehaviorSubject<Article[]> = new BehaviorSubject<Article[]>([]);//empty array
  public articles: Observable<Article[]> = this._articles.asObservable();
  
  //added for sorting direction / votes, time
  private _sortByDirectionSubject: BehaviorSubject<number> = new BehaviorSubject<number>(1);  
  private _sortByFilterSubject: BehaviorSubject<ArticleSortOrderFn> = new BehaviorSubject<ArticleSortOrderFn>(sortByTime);
  public orderedArticles: Observable<Article[]>;

  private _filterBySubject: BehaviorSubject<string> = new BehaviorSubject<string>('');

  private refreshSubject: BehaviorSubject<string> = new BehaviorSubject<string>(this.source);
  private _sourceSubject: BehaviorSubject<any> = new BehaviorSubject<any>([]);
  public sources: Observable<any> = this._sourceSubject.asObservable();
  


  public getArticleList(sourceKey = this.source): void {//added argument sourceKey for updateSourceKey
    console.log('ArticleService getArticleList');
    this._makeHttpRequests(this.path, sourceKey).subscribe((result: Newsresult)=> {
      //console.log('newResult:', result);
      if(result.status === 'ok'){        
          let tempArticleList = result.articles.map(article => {           
          article = new Article(article.title, article.description, article.urlToImage, article.publishedAt, article.votes);          
          return article;
        })        
        this._articles.next(tempArticleList);
      }
    });
  }

  private _makeHttpRequests(path:string, sourceKey?:string) : Observable<any> {    
    let params;
    let url;
    if(undefined !== sourceKey){
      params = new HttpParams().set('sources', sourceKey).set('apiKey', environment.apiKey);    
      url = environment.baseUrl + path;
    }else {
      params = new HttpParams().set('language', 'en').set('apiKey', environment.apiKey);
      url = environment.baseUrl + this.sourcePath;
    }
        
    return this.httpClient.get(url, {params});
  }

  //added for sorting
  public sortBy(filter: string, direction: number): void{
    console.log('ArticleService sortBy');
    //update the different subjects
    this._sortByDirectionSubject.next(direction);
    this._sortByFilterSubject.next(sortFns[filter]);//sortFns.Time or sortFns.xxx
  }

  //filter by title - serach
  public filterByTitle(filter:string):void{
    console.log('filterBy - filter:', filter);
    this._filterBySubject.next(filter);
  }

  public getSourceList(): void {
    console.log('ArticleService getSourceList');
    this._makeHttpRequests(this.path).subscribe((result: any)=> {
      console.log('getSourceList - result:', result.sources);
      this._sourceSubject.next(result.sources);
    });
  }

  public updateSourceKey(sourceKey: string): void {
    console.log('ArticleService - updateSourceKey:', sourceKey);
    this.refreshSubject.next(sourceKey);
  }
}


/**
 * <Subject>
 * A Subject is a special type of Observable that allows values to be multicasted 
 * to many Observers. Subjects are like EventEmitters
 * 
 * <BehaviorSubject>
 * A variant of Subject that requires an initial value and 
 * emits its current value whenever it is subscribed to.
 * 
 * <combineLatest>
 * Combines multiple Observables to create an Observable whose values 
 * are calculated from the latest values of each of its input Observables
 * 
 * <map>
 * Applies a given project function to each value emitted by the source Observable, 
 * and emits the resulting values as an Observable
 * 
 * <bind>
 * For a given function, creates a bound function that has the same body 
 * as the original function. The this object of the bound function is associated 
 * with the specified object, and has the specified initial parameters.
 * (or)
 * The bind() method creates a new function that, when called, 
 * has its 'this' keyword set to the provided value, 
 * with a given sequence of arguments preceding any provided 
 * when the new function is called.
 * (or)
 * You can use call()/apply() to invoke the function immediately. 
 * bind() returns a bound function that, when executed later, 
 * will have the correct context ("this") for calling the original function. 
 * So bind() can be used when the function needs to be called later in 
 * certain events when it's useful.
 */